html {
	font-size: 62.5%;
}

body {
	background: _palette(bg);
	color: _palette(fg);
	font-family: _font(family);
	font-size: 1.8em;
	line-height: 1.6;
	font-weight: 600;
	-webkit-font-smoothing: antialiased;
}

h1,
.h1 {
	color: _palette(fg);
	font-family: _font(family-h1);
	font-size: 2.4rem;
	font-weight: 600;
	line-height: 1;
	margin: 0;
}

h2,
.h2 {	
	color: _palette(fg-alt);
	display: block;
	font-family: _font(family-h2);
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: .2rem;
    line-height: 1;
    margin: 0 0 1rem;
    text-transform: uppercase;
} 

h3,
.h3 {
	color: _palette(fg);
	font-family: _font(family-h3);
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 1;
	margin: 0;

}

p {
	margin: 0;
}

a {
	color: _palette(fg);
	text-decoration: none;
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: none;	
}

img {
	display: block;
	max-width: 100%;
}

hr { //defines thematic change
	background: _palette(fg-alt);
	border: 0;
	display: block;
	height: 1px;
	margin: 3rem 0;

	@media (min-width: $breakpoint-medium) {
		margin: 4.5rem 0;
	}
}

abbr[title] {
	border-bottom: 1px dotted _palette(fg);
	text-decoration: none;
}

// dl - description list
// dt - defines terms/names
// dd - describes each term/name
dl {
	margin: 0 0 3rem;

	dt,
	dd {
		font-family: $font-lekton;		
	}

	dt {
		font-weight: 600;
	}

	dd {
		font-weight: 400;
	}
}

.responsive-table {
	margin-bottom: 3rem;
	
	@media (max-width: $breakpoint-xsmall) {
		display: block;	
		overflow-x: scroll;
		max-width: $breakpoint-xsmall;

		th,
		td {
			min-width: 10rem;
		}
	}
}

table {
	border-collapse: collapse;	
	width: 100%;

	thead {
		td,
		th {
			font-weight: 600;
		}
	}

	tbody > tr:nth-child(odd) > td, 
	tbody > tr:nth-child(odd) > th {
		background: _palette(bg-alt);
	}

	th,
	td {
		font-family: _font(family-fixed);
		font-weight: 400;
		text-align: left;
		padding: .5rem 1rem;
	}
}
s
// CONTAINER

.container {
	display: block;
	@include container-width;
	@include clearfix; 
}

.main-container{
	display: block;
	@include container-width;
}
 
// CONTENT

.content {
	background: _palette(bg);
	padding: 7rem 0 0;
	margin-bottom: 31.24rem;
	position: relative;
	top: 0;
	left: 0;
	z-index: 1;

	@media (min-width: $breakpoint-medium) {
		padding: 21rem 0 0;
		margin-bottom: 23.42rem;
	}
}


// OTHERS

.nav {
	float: right;
}



.inner {
	.content {
		display: block;

		> * {
			margin-left: 0;
			margin-bottom: _size(element-margin);
		}
	}
}


.intro,
.projects {
	padding: 0 0 5rem;
}



