
.header-container {
	background: _palette(bg);
	display: grid;
	grid-template-columns: auto auto; 
	@include container-width;
	@include clearfix;
	padding-top: 3rem;
	
	.header-logo {
		display: inline-grid;
		grid-template-areas: one second;
		justify-content: flex-start;
		
		p {
			color: _palette(fg-alt);
			font-family: _font(family-h2);
			font-size: 1.2rem;
			font-weight: 400;
			letter-spacing: .3rem;
			line-height: 1;
			margin: 0 0 1rem;
		}
		img {
			grid-area: one;
		}

		span {
			grid-area: second;
			text-align: left;
			margin-left: 2.2rem;
		}

	}
	.main_menu {
		align-content: end;
		grid-column-start: 2;
		text-align: right;
	}
}

