/* Fonts
 ****************************************************************/

 $font-default: Helvetica, Arial, sans-serif;
 $font-poppins: 'Poppins', $font-default;
 $font-lekton: 'Lekton', $font-default;
 
 $font: (
	 family:				('Source Sans Pro', Helvetica, sans-serif),
	 family-h1:				($font-poppins, monospace),
	 family-h2:				($font-lekton, monospace),
	 family-h3:				($font-lekton, monospace),
	 weight:				300,
	 weight-bold:			600,
	 letter-spacing:		0.025em,
	 letter-spacing-alt:	0.25em
 );
 
 /* Colors
  ****************************************************************/
 
 $palette: (
	 bg:				#ffffff,
	 bg-alt:			rgb(159, 192, 219),
	 bg-escuro:			#7c6e6e,
	 fg:				#2c2c2c,
	 fg-bold:			#5f5555,
	 fg-light:			rgba(244,244,255,0.2),
	 form-bg:			#3b383d,
	 form-fg:			#c57d7d,
	 border:			rgba(212,212,255,0.1),
	 border-bg:			rgba(212,212,255,0.035),
	 highlight:			#507545
 );
 
 
 /* Size.
  ****************************************************************/
 
 // Breakpoint
 $breakpoint-xsmall: 400px;
 $breakpoint-small: 550px;
 $breakpoint-medium: 750px;
 $breakpoint-large: 1000px;
 $breakpoint-xlarge: 1200px;
 
 // size
 $size: (
	 border-radius:		4px,
	 element-height:		2.75em,
	 element-margin:		2em,
	 inner:				65em
 );
 
// Site Max Width

$site-width: 140rem;


 /* others
  ****************************************************************/
 
 $duration: (
 	menu:				0.35s,
 	transition:			0.2s,
 	banner:				2.5s
 );
 
