
@function val($map, $keys...) {

	@if nth($keys, 1) == null {
		$keys: remove-nth($keys, 1);
	}

	@each $key in $keys {
		$map: map-get($map, $key);
	}

	@return $map;

}

@function _duration($keys...) {
	@return val($duration, $keys...);
}

@function _font($keys...) {
	@return val($font, $keys...);
}

@function _misc($keys...) {
	@return val($misc, $keys...);
}

@function _palette($keys...) {
	@return val($palette, $keys...);
}

@function _size($keys...) {
	@return val($size, $keys...);
}